/* Navbar Base */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #1c1d1f;
  padding: 16px 32px;
  color: white;
  position: relative;
  top: 0;
  z-index: 100;
  font-size: 16px;
}

/* Left Section */
.navbar-left {
  display: flex;
  align-items: center;
  gap: 25px;
}

.navbar-logo {
  font-size: 26px;
  font-weight: bold;
  color: #fff;
  text-decoration: none;
}

.navbar-link {
  color: #ddd;
  text-decoration: none;
  font-size: 16px;
  transition: color 0.2s ease;
}

.navbar-link:hover {
  color: #fff;
}

/* Explore Dropdown */
.explore-container {
  position: relative;
  cursor: pointer;
}

.explore-link {
  color: #ddd;
  font-size: 16px;
}

.explore-dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  background: #1c1d1f;
  border: 1px solid #333;
  border-radius: 6px;
  padding: 8px 0;
  min-width: 180px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  z-index: 200;
}

.explore-item {
  width: 100%;
  padding: 8px 14px;
  background: none;
  border: none;
  color: #ddd;
  text-align: left;
  cursor: pointer;
  font-size: 15px;
  transition: background 0.2s;
}

.explore-item:hover {
  background: #2a2b2e;
  color: #fff;
}

.explore-loading {
  padding: 12px 14px;
  color: #999;
  text-align: center;
  font-size: 14px;
}

/* Search */
.navbar-search input {
  width: 320px;
  padding: 10px 14px;
  border-radius: 6px;
  border: 1px solid #444;
  outline: none;
  background-color: #2a2b2e;
  color: white;
}

.navbar-search input::placeholder {
  color: #aaa;
}

/* Right Section */
.navbar-right {
  display: flex;
  align-items: center;
  gap: 20px;
}

/* Cart Icon */
.cart-icon {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ddd;
  text-decoration: none;
  transition: color 0.2s ease;
  padding: 8px;
  border-radius: 6px;
}

.cart-icon:hover {
  color: #fff;
  background-color: #2a2b2e;
}

.cart-badge {
  position: absolute;
  top: -2px;
  right: -2px;
  background-color: #ff6b35;
  color: white;
  border-radius: 50%;
  min-width: 18px;
  height: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 11px;
  font-weight: bold;
  border: 2px solid #1c1d1f;
}

/* Profile Avatar */
.navbar-avatar {
  cursor: pointer;
  position: relative;
  transition: transform 0.2s ease;
}

.navbar-avatar:hover {
  transform: scale(1.05);
}

.navbar-avatar .avatar {
  border: 2px solid rgba(99, 102, 241, 0.3);
  box-shadow: 0 2px 8px rgba(99, 102, 241, 0.2);
  transition: all 0.2s ease;
}

.navbar-avatar .avatar:hover {
  border-color: rgba(99, 102, 241, 0.8);
  box-shadow: 0 4px 12px rgba(99, 102, 241, 0.3);
}

/* Auth Buttons */
.auth-buttons {
  display: flex;
  align-items: center;
  gap: 20px;
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0 22px;
  height: 48px;
  min-width: 110px;
  border-radius: 6px;
  font-size: 15px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.2s ease;
  background-color: #e59400;
  color: #fff;
  text-decoration: none;
}

.auth-btn:hover {
  background-color: #c57f00;
}

.logout-btn {
  background-color: #dc3545;
  color: #fff;
  border: none;
}

.logout-btn:hover {
  background-color: #b02a37;
}

/* Responsive */
@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    align-items: center;
    padding: 14px 20px;
    gap: 12px;
  }

  .navbar-left,
  .navbar-search,
  .navbar-right {
    width: 100%;
    justify-content: center;
  }

  .navbar-search input {
    width: 90%;
  }

  .auth-buttons {
    gap: 10px;
  }
}
/* Notification Bell */
.notification-bell {
  position: relative;
  cursor: pointer;
  display: flex;
  align-items: center;
  padding: 8px;
  border-radius: 6px;
  transition: background 0.2s ease, color 0.2s ease;
  color: #ddd;
}

.notification-bell:hover {
  background-color: #2a2b2e;
  color: #fff;
}

/* Red dot for unread notifications on bell */
.bell-unread-dot {
  position: absolute;
  top: 4px;
  right: 4px;
  width: 10px;
  height: 10px;
  background: #ff6b35;
  border-radius: 50%;
  border: 2px solid #1c1d1f;
}

/* Notification Dropdown */
.notification-dropdown {
  position: absolute;
  right: 0;
  top: 48px;
  width: 300px;
  max-height: 400px;
  background: #1c1d1f;
  border: 1px solid #333;
  border-radius: 8px;
  box-shadow: 0 4px 16px rgba(0,0,0,0.5);
  padding: 8px 0;
  overflow-y: auto;
  z-index: 999;
}

/* Notification Item */
.notification-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 16px;
  cursor: pointer;
  transition: background 0.2s ease;
  border-bottom: 1px solid #333;
}

.notification-item:last-child {
  border-bottom: none;
}

.notification-item:hover {
  background-color: #2a2b2e;
}

/* Unread */
.notification-item.unread {
  background-color: #3b1d1d;
}

/* Read */
.notification-item.read {
  background-color: #1c1d1f;
  color: #aaa;
}

/* Notification content */
.notification-content {
  flex: 1;
}

.notification-content strong {
  color: #fff;
}

.notification-content p {
  margin: 4px 0 0 0;
  font-size: 13px;
  color: #ccc;
}

/* Red dot for unread inside dropdown */
.unread-dot {
  width: 10px;
  height: 10px;
  background: #ff6b35;
  border-radius: 50%;
  margin-left: 8px;
}

/* Green check for read messages */
.read-icon {
  margin-left: 8px;
  color: #4caf50;
}

/* Empty state */
.notification-item.empty {
  text-align: center;
  padding: 16px 0;
  font-size: 14px;
  color: #888;
}

